import React, { useEffect, useState, useRef } from "react";

import { IWorkflowStep } from "../../types/workflow.type";

import Bond from "../../assets/workflow/bond.svg";

const WorkflowStep = ({
  step,
  scroll,
  stepNumber,
  className,
}: {
  step: IWorkflowStep;
  scroll: boolean;
  stepNumber: number;
  className?: string;
}) => {
  const div = useRef<HTMLDivElement>(null);
  const [coverWidth, setCoverWidth] = useState<"w-[100%]" | "w-0">("w-[100%]");

  useEffect(() => {
    if (!div.current || !window) return;
    const divBottom = div.current.getBoundingClientRect().bottom;

    if (window.innerHeight > divBottom) setCoverWidth("w-0");
  }, [scroll]);

  const {
    html,
    frontmatter: { title },
  } = step;

  return (
    <div
      ref={div}
      className={`workflow-step relative flex w-full items-center justify-between text-main-100 gap-5 md:gap-28 max-w-4.5xl ${className}`}
    >
      <div
        className={`hidden md:block cover absolute -top-2 -bottom-2 z-10 bg-secondary-100 transition-all duration-1000 ${coverWidth}`}
      />
      <p className="text-center px-5 md:px-20 text-[88px] sm:text-[148px] font-bold shrink-0 py-4">
        {stepNumber}
      </p>
      <div className="flex flex-col flex-1 gap-2.5">
        <h2 className="font-bold text-2xl sm:text-3xl">{title}</h2>
        <p
          className="w-full font-normal text-sm sm:text-lg"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </div>
    </div>
  );
};

export default WorkflowStep;

