import React from "react";
import { graphql, useStaticQuery } from "gatsby";

import { IWhyUsQueryResult, IWhyUsReason } from "../../types/whyUs.type";
import { ILang, Theme } from "../../types/common.type";

import TemplateSection from "../common/templateSection";
import WhyUsReason from "./whyUsReason";

const WhyUsSection = ({ lang }: ILang) => {
  const whyUsQueryData = useStaticQuery(query) as IWhyUsQueryResult;

  let whyUsReasons: IWhyUsReason[] | null | undefined;
  if (lang in whyUsQueryData)
    whyUsReasons = whyUsQueryData?.[lang]?.nodes as
      | IWhyUsReason[]
      | null
      | undefined;
  else whyUsQueryData?.en?.nodes;

  const sectionTitle =
    whyUsQueryData?.markupData?.nodes?.find(
      (node) => node.fields.language === lang
    )?.frontmatter?.whyUs?.title ?? "why us";

  return (
    <>
      {whyUsReasons?.length ? (
        <TemplateSection theme={Theme.dark} title={sectionTitle} borderB>
          <div className="columns-1 md:columns-2 w-full gap-x-28 max-w-4.5xl">
            {whyUsReasons.map((reason) => (
              <WhyUsReason key={reason.id} reason={reason} />
            ))}
          </div>
        </TemplateSection>
      ) : null}
    </>
  );
};

export default WhyUsSection;

const query = graphql`
  {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "whyUs" }, language: { eq: "en" } }
      }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
        }
        fields {
          language
        }
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "whyUs" }, language: { eq: "ru" } }
      }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
        }
        fields {
          language
        }
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          whyUs {
            title
          }
        }
      }
    }
  }
`;
